@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap');
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto';
}

svg path {
  fill: inherit;
  stroke: inherit;
  stroke-width: inherit;
}

.text_bold {
  font-weight: 500;
}
